@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
:root {
  --font-robot: "Roboto", sans-serif;
}

:root {
  --white-bg: #ffffff;
  --grey-color: #a7a7a7;
  --light-grey: #f8f8f8;
  --dark-grey: #efefef;
  --black-color: #212b33;
  --orange-color: #f27c4a;
  --green-color: #41b874;
  --red-color: #eb5252;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

html, body, .menu-wrap {
  height: 100vh;
  min-height: 100% !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-robot);
  background: var(--grey-color);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.main-wrap {
  padding: 1.875rem 2rem 0 2rem;
  background: var(--light-grey);
}

.menu-wrap {
  justify-content: center;
  background: var(--white-bg);
  display: flex;
}
.menu-wrap__menu {
  list-style: none;
  padding: 0;
  margin: 3rem 0 0 0;
}
.menu-wrap__menu li {
  margin-bottom: 3.5rem;
}
.menu-wrap__menu li a {
  font-size: 1.25rem;
  color: var(--grey-color);
}
.menu-wrap__menu li:hover a, .menu-wrap__menu li.active a {
  color: var(--orange-color);
}
.menu-wrap__menu li:hover a::before, .menu-wrap__menu li.active a::before {
  content: "";
  background-color: var(--orange-color);
}

.header {
  display: grid;
  grid-template-columns: 15.8125rem 19.75rem 2.5rem 7.3125rem 9.1875rem;
  grid-column-gap: 2.125rem;
  border-bottom: 1px solid #ececec;
  padding-bottom: 1.75rem;
  margin-bottom: 1.875rem;
}
.header__title span {
  font-size: 0.75rem;
  color: var(--grey-color);
  margin-bottom: 0.625rem;
  display: flex;
}
.header__title h2 {
  font-size: 1.25rem;
  font-weight: 500;
}

.order-wrap__widget {
  background-color: var(--white-bg);
  border-radius: 1.25rem;
  padding: 1.625rem;
}
.order-wrap__widget-title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
}
.order-wrap__widget-menulist ul {
  padding: 0;
  margin: 0 0 1.25rem 0;
  list-style: none;
}
.order-wrap__widget-menulist ul li {
  display: inline-flex;
  justify-content: center;
  margin-left: 0.3125rem;
}
.order-wrap__widget-menulist ul li a {
  padding: 0.4375rem 1rem;
  border-radius: 1.5rem;
  background: var(--grey-color);
  color: var(--light-grey);
}
.order-wrap__widget-menulist ul li:hover a, .order-wrap__widget-menulist ul li.active a {
  background: var(--black-color);
  color: var(--white-bg);
}
.order-wrap__widget-menulist ul li:first-child {
  margin-left: 0;
}
.order-wrap__widget-itemlist {
  display: flex;
  margin-bottom: 1.25rem;
  border-bottom: 0.0625rem solid var(--light-grey);
  padding-bottom: 1.25rem;
}
.order-wrap__widget-itemlist--image {
  width: 5.4375rem;
  flex-shrink: 0;
  margin-right: 1.125rem;
}
.order-wrap__widget-itemlist--image img {
  border-radius: 0.9375rem;
  width: 100%;
  height: auto;
}
.order-wrap__widget-itemlist--content h3 {
  font-size: 1rem;
  color: var(--black-color);
  margin-bottom: 0.3125rem;
}
.order-wrap__widget-itemlist--content p {
  font-size: 0.75rem;
  margin-bottom: 0.3125rem;
}
.order-wrap__widget-itemlist--content ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.order-wrap__widget-itemlist--content ul li {
  padding: 0 0 0 0.9375rem;
  margin-right: 0.625rem;
  display: inline-flex;
  position: relative;
  justify-content: center;
}
.order-wrap__widget-itemlist--content ul li::before {
  content: ".";
  color: #bcbcbc;
  font-weight: bold;
  display: inline-block;
  width: 1rem;
  margin-left: 0;
  font-size: 2rem;
  position: absolute;
  top: -1.3rem;
  left: 0;
}
.order-wrap__widget-itemlist--content .price {
  font-size: 1rem;
  color: var(--black-color);
  font-weight: bold;
}
.order-wrap__widget-item-rate {
  display: flex;
  justify-content: space-between;
  padding: 0 0 0.625rem 0;
}
.order-wrap__widget-item-rate .item-label {
  color: var(--grey-color);
  font-size: 0.875rem;
}
.order-wrap__widget-item-rate .item-price {
  color: var(--green-color);
  font-size: 0.875rem;
}
.order-wrap__widget .discount {
  color: var(--red-color);
}
.order-wrap__widget .total {
  border-top: 1px solid var(--light-grey);
  padding-top: 0.625rem;
}
.order-wrap__payment h4 {
  margin-bottom: 1.25rem;
  margin-top: 0.625rem;
}
.order-wrap__payment ul {
  padding: 0;
  margin: 0 0 0.9375rem 0;
  list-style: none;
}
.order-wrap__payment ul li {
  display: inline-flex;
  justify-content: center;
  margin-left: 0.5625rem;
}
.order-wrap__payment ul li a {
  padding: 0.4375rem 1.25rem;
  border-radius: 1.5rem;
  background: var(--grey-color);
  color: var(--light-grey);
}
.order-wrap__payment ul li:hover a, .order-wrap__payment ul li.active a {
  background: var(--black-color);
  color: var(--white-bg);
}
.order-wrap__payment ul li:first-child {
  margin-left: 0;
}

.content-wrap {
  padding: 0;
}

.second_menu {
  margin: 0 0 2.1875rem 0;
}
.second_menu ul {
  list-style: none;
  padding: 0;
}
.second_menu ul li {
  display: inline-flex;
  justify-content: center;
  margin: 0 0.5625rem 0.625rem;
}
.second_menu ul li a {
  padding: 0.625rem 0.8125rem;
  border-radius: 1.5rem;
  background-color: #efefef;
  color: var(--grey-color);
}
.second_menu ul li a i {
  border-radius: 50%;
  background: #fff;
  padding: 0.4375rem;
  margin-right: 0.5rem;
}
.second_menu ul li:first-child {
  margin-left: 0;
}
.second_menu ul li:first-child {
  margin-left: 0;
}
.second_menu ul li:hover a, .second_menu ul li.active a {
  color: var(--white-bg);
  background: var(--orange-color);
}
.second_menu ul li:hover a i, .second_menu ul li.active a i {
  color: var(--orange-color);
}

.content-top {
  overflow: hidden;
  clear: both;
}
.content-top h2 {
  float: left;
}
.content-top span {
  float: right;
}

.block__block-list {
  width: 100%;
  margin-bottom: 1rem;
  background-color: var(--white-bg);
  border-radius: 1.25rem;
  padding: 0.625rem;
}
.block__block-list:nth-child(odd) {
  margin-left: 0;
}
.block__block-list__content {
  display: flex;
  margin-bottom: 1.25rem;
}
.block__block-list__content .content-image {
  width: 5.4375rem;
  flex-shrink: 0;
  margin-right: 1.125rem;
}
.block__block-list__content .content-image img {
  border-radius: 0.9375rem;
  width: 100%;
  height: auto;
}
.block__block-list__content .content-dsc h3 {
  font-size: 1rem;
  color: var(--black-color);
  margin-bottom: 0.3125rem;
}
.block__block-list__content .content-dsc p {
  font-size: 0.75rem;
  margin-bottom: 0.3125rem;
}
.block__block-list__content .content-dsc .price {
  font-size: 1rem;
  color: var(--black-color);
  font-weight: bold;
}
.block__block-list__type {
  margin-bottom: 1.25rem;
}
.block__block-list__type h4 {
  font-size: 0.875rem;
  margin-bottom: 0.625rem;
}
.block__block-list__type ul {
  padding: 0;
  margin: 0 0 0.9375rem 0;
  list-style: none;
}
.block__block-list__type ul li {
  display: inline-flex;
  justify-content: center;
  margin-left: 0.5625rem;
}
.block__block-list__type ul li a {
  padding: 0.4375rem 1.25rem;
  border-radius: 1.5rem;
  background: var(--grey-color);
  color: var(--light-grey);
}
.block__block-list__type ul li:hover a, .block__block-list__type ul li.active a {
  background: var(--black-color);
  color: var(--white-bg);
}
.block__block-list__type ul li:first-child {
  margin-left: 0;
}

.btn-normal {
  background: var(--light-grey);
  border-radius: 1.5rem;
  border: 0;
  width: 100%;
  height: 2.5rem;
  color: var(--black-color);
}
.btn-normal:hover, .btn-normal.active {
  background: var(--orange-color);
  color: var(--white-bg);
}